  private markFormGroupTouched(formGroup: FormGroup) {
    try {
      (<any>Object).values(formGroup.controls).forEach(control => {
        control.markAsTouched();
        if (control.controls) {
          control.controls.forEach(c => this.markFormGroupTouched(c));
        }
      });
    } catch (ex) {
      // Code to handle exception
    }


}
addNewActivitesAfterEdit(originalObject) {
 
  this.isFieldValid = true;
  let trainerDetailMappingId = 0;
  let data: any;

  this.activitiesList.forEach(element => {
    if (element.subClassCategoryId === originalObject.id) {
      data = element;
    }
  });
  const indexremove = this.deletedActivitiesList1.indexOf(originalObject.id);
  if (indexremove > -1) {
    trainerDetailMappingId = this.deletedActivitiesList[indexremove];
  }
  if (!data) {
    if(this.editingPackage){
      data = { 'id': trainerDetailMappingId,'packageId':this.packageId,'subClassCategoryId':originalObject.id, 'categoryId': originalObject.classCategoryResponseDto.id,'status':true ,'categoryName': originalObject.name };

    }else{
      data = { 'id': trainerDetailMappingId,'subClassCategoryId':originalObject.id, 'categoryId': originalObject.classCategoryResponseDto.id,'status':true ,'categoryName': originalObject.name };

    }

  }
  if (indexremove > -1) {
    this.deletedActivitiesList.splice(indexremove, 1);
    this.deletedActivitiesList.splice(indexremove, 1);
  }
  let categoryIdList=[];
  this.activitiesList.forEach(element => {
    categoryIdList.push(element.subClassCategoryId);
  });
  if (originalObject.id && categoryIdList.indexOf(data.subClassCategoryId) === -1) {
    this.activitiesList.push(data);

  }
}


addNewActivites($event) {
 
  this.isFieldValid = true;
  let trainerDetailMappingId = 0;
  let data: any;

  this.activitiesList.forEach(element => {
    if (element.subClassCategoryId === $event.originalObject.id) {
      data = element;
    }
  });
  const indexremove = this.deletedActivitiesList1.indexOf($event.originalObject.id);
  if (indexremove > -1) {
    trainerDetailMappingId = this.deletedActivitiesList[indexremove];
  }
  if (!data) {
    if(this.editingPackage){
      data = { 'id': trainerDetailMappingId,'packageId':this.packageId,'subClassCategoryId':$event.originalObject.id, 'categoryId': $event.originalObject.classCategoryResponseDto.id,'status':true ,'categoryName': $event.originalObject.name };

    }else{
      data = { 'id': trainerDetailMappingId,'subClassCategoryId':$event.originalObject.id, 'categoryId': $event.originalObject.classCategoryResponseDto.id,'status':true ,'categoryName': $event.originalObject.name };

    }

  }
  if (indexremove > -1) {
    this.deletedActivitiesList.splice(indexremove, 1);
    this.deletedActivitiesList.splice(indexremove, 1);
  }
  let categoryIdList=[];
  this.activitiesList.forEach(element => {
    categoryIdList.push(element.subClassCategoryId);
  });
  if ($event.originalObject.id && categoryIdList.indexOf(data.subClassCategoryId) === -1) {
    this.activitiesList.push(data);

  }
}
deleteActivities(value: ClassCategoryList) {
  const index = this.activitiesList.indexOf(value);
  this.activitiesList.splice(index, 1);
  const indexremove = this.deletedActivitiesList.indexOf(value.id);
  if (indexremove == -1) {
    this.deletedActivitiesList.push(value.id);
    this.deletedActivitiesList1.push(value.classCategoryId);
  }
}


