import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddEditPackageComponent } from './add-edit-package.component';


const routes: Routes = [{ path: '', component: AddEditPackageComponent },
{path:':id',component: AddEditPackageComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddEditPackageRoutingModule { }



