import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { AddEditPackageRoutingModule } from './add-edit-package-routing.module';
import { AddEditPackageComponent } from './add-edit-package.component';
import { CoreModule } from 'core/core.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonComponentsModule } from 'common/common.module';


@NgModule({
  declarations: [AddEditPackageComponent],
  imports: [
    CommonModule,
    CoreModule,
    FormsModule,
    ReactiveFormsModule,
    CommonComponentsModule,
    AddEditPackageRoutingModule
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]

})
export class AddEditPackageModule { }


